import { EmailOutlined, Facebook, Home, Instagram, LocalPhone, Twitter, YouTube } from '@mui/icons-material'
import styled from 'styled-components'
import { mobile } from '../responsive'


const Container = styled.div`
     height: 50vh;
     display:flex;
     ${mobile({ flexDirection: "column" })}
`
const Left = styled.div`
     flex:1;
     display:flex;
     flex-direction: column;
     padding:20px;
`
const Logo = styled.h1``

const Desc = styled.p`
     margin:20px 0px;
`

const SocialCont = styled.div`
     display:flex;
`

const SocialIcons = styled.div`
     width:40px;
     height:40px;
     order-raduis: 50%;
     color:white;
     background-color: #${props=> props.color};
     display:flex;
     align-items: Center;
     justify-content: center;
     margin-right:20px;

`

//********************************* */

const Center = styled.div`
     flex:1;
     padding: 20px;
     ${mobile({ display: "none" })}
`

const Title = styled.h3`
  margin-bottom: 30px;
`;

const List = styled.ul`
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
`;

const ListItem = styled.li`
  width: 50%;
  margin-bottom: 10px;
`;

//********************************* */
const Right = styled.div`
     flex:1;
     padding: 20px;
     ${mobile({ backgroundColor: "lightgrey" })}
`
const ContactItem = styled.div`
  margin-bottom: 20px;
  display: flex;
  align-items: center;
`;

const Payment = styled.img`
    width: 50%;
`;


const Footer = () => {
  return (
    <Container>
       <Left>
         <Logo>Fashion Store.</Logo>
         <Desc>There are many variations of passages of Lorem Ipsum available, but
          the majority have suffered alteration in some form, by injected
          humour, or randomised words which don’t look even slightly believable.</Desc>
          <SocialCont>
             <SocialIcons color='3B5999'>
                <Facebook />
             </SocialIcons>

             <SocialIcons color='E4405F'>
                <Instagram/>
             </SocialIcons>

             <SocialIcons color='55ACEE'>
                <Twitter />
              </SocialIcons>

              <SocialIcons color='E60023'>
                <YouTube/>
             </SocialIcons>
          </SocialCont>
       </Left>
       <Center>
       <Title>Useful Links</Title>
        <List>
          <ListItem>Home</ListItem>
          <ListItem>Cart</ListItem>
          <ListItem>Man Fashion</ListItem>
          <ListItem>Woman Fashion</ListItem>
          <ListItem>Accessories</ListItem>
          <ListItem>My Account</ListItem>
          <ListItem>Order Tracking</ListItem>
          <ListItem>Wishlist</ListItem>
          <ListItem>Wishlist</ListItem>
          <ListItem>Terms</ListItem>
        </List>
       </Center>
       <Right>
        <Title>Contacts</Title>
        <ContactItem>
         <Home style={{marginRight: "10px"}}/>
           44 Riebeek Street, Cape Town, South Africa
        </ContactItem>

        <ContactItem>
          <LocalPhone style={{marginRight: "10px"}}/> 
           +27(0)844848030
        </ContactItem>

        <ContactItem>
        <EmailOutlined style={{marginRight: "10px"}}/>
           akoatemsam2003@gmail.com
        </ContactItem>

        <Payment src="https://i.ibb.co/Qfvn4z6/payment.png" />
       </Right>
    </Container>
  )
}

export default Footer